import request from '@/utils/request'

// 获取小车充电档位配置列表
export function getChargeGearList(data) {
  return request.post('/amrChargeConfig/list', data)
}

// 新增小车对应充电档位配置
export function addChargeGear(data) {
  return request.post('/amrChargeConfig/add', data)
}

// 修改小车对应充电档位配置
export function updateChargeGear(data) {
  return request.post("/amrChargeConfig/update", data)
}

// 删除小车对应充电档位配置
export function deleteChargeGear({ id, uuid }) {
  return request.delete(`/amrChargeConfig/delete/${id}/${uuid}`)
}