import request from '@/utils/request'

// 获取所有机器人列表☑️
export function getVehicleList(data) {
  return request.post('/amr/getAmrPageList', data)
}

// 获取在线机器列表☑️
export function getOnlineVehicleList() {
  return request.post('/amr/onlineAmr')
}

// 获取小车类型列表☑️
export function getVehicleTypeList() {
  return request.get('/amr/listAmrType')
}

// 添加设备☑️
export function addVehicle(data) {
  return request.post('/amr/register', data)
}

// 修改设备 名称☑️
export function updateVehicle(data) {
  return request.post('/amr/editorAmr', data)
}

// 启用/禁用小车☑️
export function toggleVehicleEnable(data) {
  return request.get('/amr/enable', data)
}

// 删除设备☑️
export function deleteVehicle(id) {
  return request.post('/amr/deleteAmr/' + id)
}

// 获取设备详情☑️
export function getVehicleDetail(data) {
  return request.get('/amr/getDetailAmr', data)
}

// 修改小车区域☑️
export function updateVehicleArea(data) {
  return request.get('/amr/updateArea', data)
}

// 修改小车分组
export function updateVehicleGroup(data) {
  return request.get('/amr/updateGroup', data)
}

// 修改小车充电类型
export function updateVehicleModel(data) {
  return request.get('chargeConf/update/amrAdaptation', data)
}

// 修改小车充电桩
export function updateVehicleCharge(data) {
  return request.get('chargeConf/update/amrBind', data)
}

// 修改小车休息点
export function updateVehicleRest(data) {
  return request.get('rest/update/amrBind', data)
}