import request from '@/utils/request'

// 获取充电桩类型☑️
export function getChargePileModelList() {
  return request.get('/chargePile/model/findAll')
}

// 获取充电桩列表☑️
export function getChargePileList(data) {
  return request.post('/chargePile/page', data)
}

// 新增充电桩☑️
export function addChargePile(data) {
  return request.post('/chargePile/add',data)
}

// 启用/禁用充电桩☑️
export function changeChargePileEnabld({ id, enable }) {
  return request.get('/chargePile/enable?id=' + id + '&state=' + enable,)
}

// 修改充电桩☑️
export function changeChargePileModel({ id, modelId }) {
  return request.get('chargePile/bindModel?id=' + id + '&modelId=' + modelId)
}

// 删除充电桩☑️
export function deleteChargePile(id) {
  return request.get('/chargePile/delete?id=' + id)
}

// 获取地图点位列表，用来下拉选择
export function getMapVertexes(mapId) {
  return request.get('/map/getMapVertexes/' + mapId)
}