<template>
  <span v-if="!isStartEdit"
    ><i class="el-icon-edit edit" @click="startEdit"></i
  ></span>
  <span v-else
    ><i class="el-icon-check check" @click="checkEdit"></i
    ><i class="el-icon-close close" @click="closeEdit"></i
  ></span>
</template>

<script>
export default {
  props: ["isEdit", "data"],
  data() {
    return {
      oldDataCopy: "", //用来还原数据
    };
  },
  computed: {
    //   编辑的数据
    editData: {
      get() {
        return this.data;
      },
      set(val) {
        this.$emit("update:data", val);
      },
    },
    // 是否正在编辑
    isStartEdit: {
      get() {
        return this.isEdit;
      },
      set(val) {
        this.$emit("update:isEdit", val);
      },
    },
  },
  methods: {
    // 开始编辑
    startEdit() {
      this.$emit("closeAll");
      this.isStartEdit = true;
      //   保存旧数据
      if (typeof this.editData === "object") {
        this.oldDataCopy = JSON.parse(JSON.stringify(this.editData));
      } else {
        this.oldDataCopy = this.editData;
      }
      this.$emit("open");
    },
    // 结束编辑，还原数据
    closeEdit() {
      this.isStartEdit = false;
      this.editData = this.oldDataCopy;
    },
    // 发出编辑指令
    checkEdit() {
      // 不同的类型判断数据是否发生变化
      if (Array.isArray(this.editData)) {
        if (this.editData.join(",") !== this.oldDataCopy.join(",")) {
          this.$emit("checkEdit", this.editData);
        } else {
          this.isStartEdit = false;
        }
      } else {
        if (this.editData !== this.oldDataCopy) {
          this.$emit("checkEdit", this.editData);
        } else {
          this.isStartEdit = false;
        }
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.edit,
.check,
.close {
  font-size: 20px;
  cursor: pointer;
}
.edit {
  margin-left: 5px;
}
.check {
  margin: 0 5px;
  color: #67c23a;
}
</style>